<template>
  <div class="recommend">
    <div class="title">
      热销推荐
    </div>
    <ul class="recommend-list">
      <router-link
      tap="li"
      v-for="item of list"
      :key = "item.id"
      class="recommend-item"
      :to ="'/detail/'+item.id">
        <div class="recomment-pic-con">
          <img class="recomment-pic" :src="item.imgUrl" :alt="item.title">
        </div>
        <ul class="recommend-desc">
          <li class="recommend-desc-title ellipsis">{{item.title}}</li>
          <li class="recommend-desc-detail ellipsis">{{item.desc}}</li>
          <li class="check-detail">
            <button class="check-detail-btn">查看详情</button>
          </li>
        </ul>
      </router-link>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'HomeRecomment',
  props: {
    list: Array
  }
}
</script>

<style lang="scss" scoped>
  .recommend {
    background: #fff;
    padding-left: 30px;
    .title {
      font-size: 32px;
      padding: 20px 0;
    }
    .recommend-list{
      .recommend-item{
        border-bottom: 1px solid lightgrey;
        padding: 20px 0;
        display: flex;
        .recomment-pic-con{
          width: 200px;
          height: 200px;
          img{
            width:100%;
            height: 100%;
          }
        }
        .recommend-desc {
          margin-left:20px;
          flex: 1;
          min-width: 0;
          .recommend-desc-title{
            font-size:32px;
            color:#212121;
          }
          .recommend-desc-detail{
            margin-top: 20px;
            font-size:30px;
            color: #847d7d;
          }
          .check-detail{
            .check-detail-btn{
              margin-top: 20px;
              padding: 5px 20px;
              border-radius: 5px;
              background-color: #dccc28;
              color: #fff;
            }
          }
        }
      }
    }
  }
</style>



